import axios, { AxiosInstance } from 'axios';
import { Category, Question, Tip, User, QuizResult, DashboardStats, Notification, NotificationStats, NotificationFormData, ApiResponse, PaginatedResponse } from '../types';

class ApiService {
  private api: AxiosInstance;

  constructor() {
    this.api = axios.create({
      //baseURL: 'http://localhost:5000/api',
      baseURL: 'https://speak-creaft-backend.onrender.com/api',
      headers: {
        'Content-Type': 'application/json',
      },
    });

    // Add request interceptor to include auth token
    this.api.interceptors.request.use(
      (config) => {
        const token = localStorage.getItem('adminToken');
        if (token) {
          config.headers.Authorization = `Bearer ${token}`;
        }
        return config;
      },
      (error) => {
        return Promise.reject(error);
      }
    );

    // Add response interceptor to handle 401 errors
    this.api.interceptors.response.use(
      (response) => response,
      (error) => {
        if (error.response?.status === 401) {
          localStorage.removeItem('adminToken');
          window.location.href = '/login';
        }
        return Promise.reject(error);
      }
    );
  }

  // Authentication
  async login(credentials: { email: string; password: string }): Promise<ApiResponse<{ token: string; admin: any }>> {
    const response = await this.api.post('/admin/login', credentials);
    return response.data;
  }

  async logout(): Promise<ApiResponse<void>> {
    const response = await this.api.post('/admin/logout');
    return response.data;
  }

  async verifyToken(): Promise<ApiResponse<any>> {
    const response = await this.api.get('/admin/verify');
    return response.data;
  }

  // Dashboard
  async getDashboardStats(): Promise<ApiResponse<DashboardStats>> {
    const response = await this.api.get('/admin/dashboard/stats');
    return response.data;
  }

  // Categories
  async getCategories(): Promise<ApiResponse<{ categories: Category[] }>> {
    const response = await this.api.get('/admin/categories');
    return response.data;
  }

  async getCategory(id: string): Promise<ApiResponse<{ category: Category }>> {
    const response = await this.api.get(`/admin/categories/${id}`);
    return response.data;
  }

  async createCategory(categoryData: Partial<Category>): Promise<ApiResponse<{ category: Category }>> {
    const response = await this.api.post('/admin/categories', categoryData);
    return response.data;
  }

  async updateCategory(id: string, categoryData: Partial<Category>): Promise<ApiResponse<{ category: Category }>> {
    const response = await this.api.put(`/admin/categories/${id}`, categoryData);
    return response.data;
  }

  async deleteCategory(id: string): Promise<ApiResponse<void>> {
    const response = await this.api.delete(`/admin/categories/${id}`);
    return response.data;
  }

  // Questions
  async getQuestions(params?: { status?: string; search?: string; category?: string; difficulty?: string }): Promise<ApiResponse<PaginatedResponse<Question>>> {
    const response = await this.api.get('/admin/questions', { params });
    // Transform the response to match expected structure
    const data = response.data.data;
    return {
      ...response.data,
      data: {
        items: data.questions || [],
        total: data.pagination?.total || 0,
        page: data.pagination?.page || 1,
        limit: data.pagination?.limit || 10,
        totalPages: data.pagination?.pages || 1,
      }
    };
  }

  async getQuestion(id: string): Promise<ApiResponse<{ question: Question }>> {
    const response = await this.api.get(`/admin/questions/${id}`);
    return response.data;
  }

  async createQuestion(questionData: Partial<Question>): Promise<ApiResponse<{ question: Question }>> {
    const response = await this.api.post('/admin/questions', questionData);
    return response.data;
  }

  async updateQuestion(id: string, questionData: Partial<Question>): Promise<ApiResponse<{ question: Question }>> {
    const response = await this.api.put(`/admin/questions/${id}`, questionData);
    return response.data;
  }

  async deleteQuestion(id: string): Promise<ApiResponse<void>> {
    const response = await this.api.delete(`/admin/questions/${id}`);
    return response.data;
  }

  // Tips
  async getTips(params?: { status?: string; search?: string; category?: string; difficulty?: string; type?: string }): Promise<ApiResponse<PaginatedResponse<Tip>>> {
    const response = await this.api.get('/admin/tips', { params });
    // Transform the response to match expected structure
    const data = response.data.data;
    return {
      ...response.data,
      data: {
        items: data.tips || [],
        total: data.pagination?.total || 0,
        page: data.pagination?.page || 1,
        limit: data.pagination?.limit || 10,
        totalPages: data.pagination?.pages || 1,
      }
    };
  }

  async getTip(id: string): Promise<ApiResponse<{ tip: Tip }>> {
    const response = await this.api.get(`/admin/tips/${id}`);
    return response.data;
  }

  async createTip(tipData: Partial<Tip>): Promise<ApiResponse<{ tip: Tip }>> {
    const response = await this.api.post('/admin/tips', tipData);
    return response.data;
  }

  async updateTip(id: string, tipData: Partial<Tip>): Promise<ApiResponse<{ tip: Tip }>> {
    const response = await this.api.put(`/admin/tips/${id}`, tipData);
    return response.data;
  }

  async deleteTip(id: string): Promise<ApiResponse<void>> {
    const response = await this.api.delete(`/admin/tips/${id}`);
    return response.data;
  }

  // Users
  async getUsers(): Promise<ApiResponse<PaginatedResponse<User>>> {
    const response = await this.api.get('/admin/users');
    // Transform the response to match expected structure
    const data = response.data.data;
    return {
      ...response.data,
      data: {
        items: data.users || [],
        total: data.pagination?.total || 0,
        page: data.pagination?.page || 1,
        limit: data.pagination?.limit || 10,
        totalPages: data.pagination?.pages || 1,
      }
    };
  }

  async getUser(id: string): Promise<ApiResponse<{ user: User }>> {
    const response = await this.api.get(`/admin/users/${id}`);
    return response.data;
  }

  async createUser(userData: Partial<User>): Promise<ApiResponse<{ user: User }>> {
    const response = await this.api.post('/admin/users', userData);
    return response.data;
  }

  async updateUser(id: string, userData: Partial<User>): Promise<ApiResponse<{ user: User }>> {
    const response = await this.api.put(`/admin/users/${id}`, userData);
    return response.data;
  }

  async deleteUser(id: string): Promise<ApiResponse<void>> {
    const response = await this.api.delete(`/admin/users/${id}`);
    return response.data;
  }

  // Quiz Results
  async getQuizResults(): Promise<ApiResponse<PaginatedResponse<QuizResult>>> {
    const response = await this.api.get('/admin/quiz-results');
    // Transform the response to match expected structure
    const data = response.data.data;
    return {
      ...response.data,
      data: {
        items: data.quizResults || [],
        total: data.pagination?.total || 0,
        page: data.pagination?.page || 1,
        limit: data.pagination?.limit || 10,
        totalPages: data.pagination?.pages || 1,
      }
    };
  }

  async getQuizResult(id: string): Promise<ApiResponse<{ quizResult: QuizResult }>> {
    const response = await this.api.get(`/admin/quiz-results/${id}`);
    return response.data;
  }

  async createQuizResult(quizResultData: Partial<QuizResult>): Promise<ApiResponse<{ quizResult: QuizResult }>> {
    const response = await this.api.post('/admin/quiz-results', quizResultData);
    return response.data;
  }

  async updateQuizResult(id: string, quizResultData: Partial<QuizResult>): Promise<ApiResponse<{ quizResult: QuizResult }>> {
    const response = await this.api.put(`/admin/quiz-results/${id}`, quizResultData);
    return response.data;
  }

  async deleteQuizResult(id: string): Promise<ApiResponse<void>> {
    const response = await this.api.delete(`/admin/quiz-results/${id}`);
    return response.data;
  }

  // Notifications
  async getNotifications(params?: {
    page?: number;
    limit?: number;
    type?: string;
    isRead?: boolean;
    userId?: string;
  }): Promise<ApiResponse<PaginatedResponse<Notification>>> {
    const response = await this.api.get('/admin/notifications', { params });
    const data = response.data.data;
    return {
      ...response.data,
      data: {
        items: data.notifications || [],
        total: data.pagination?.total || 0,
        page: data.pagination?.page || 1,
        limit: data.pagination?.limit || 20,
        totalPages: data.pagination?.pages || 1
      }
    };
  }

  async getNotificationStats(): Promise<ApiResponse<NotificationStats>> {
    const response = await this.api.get('/admin/notifications/stats');
    return response.data;
  }

  async sendNotification(notificationData: NotificationFormData): Promise<ApiResponse<{ notifications: Notification[]; count: number }>> {
    const response = await this.api.post('/admin/notifications', notificationData);
    return response.data;
  }

  async deleteNotification(id: string): Promise<ApiResponse<void>> {
    const response = await this.api.delete(`/admin/notifications/${id}`);
    return response.data;
  }

  async markNotificationAsRead(id: string): Promise<ApiResponse<Notification>> {
    const response = await this.api.patch(`/admin/notifications/${id}/read`);
    return response.data;
  }

  async markAllNotificationsAsRead(): Promise<ApiResponse<{ updatedCount: number }>> {
    const response = await this.api.patch('/admin/notifications/read-all');
    return response.data;
  }

  // File Upload
  async uploadImage(file: File): Promise<ApiResponse<{ url: string; filename: string; originalName: string; size: number; mimetype: string }>> {
    const formData = new FormData();
    formData.append('image', file);
    
    const response = await this.api.post('/admin/upload', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    });
    return response.data;
  }
}

export const apiService = new ApiService(); 