import React, { useState } from 'react';
import {
  Button,
  Typography,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Alert,
  CircularProgress,
  Chip,
  ToggleButtonGroup,
  ToggleButton,
} from '@mui/material';
import {
  Add as AddIcon,
  Edit as EditIcon,
  Delete as DeleteIcon,
  RestoreFromTrash as RestoreIcon,
  Lightbulb,
  CloudUpload as UploadIcon,
  Image as ImageIcon,
} from '@mui/icons-material';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { apiService } from '../services/api';
import { Tip, Category } from '../types';

interface TipFormData {
  title: string;
  content: string;
  categoryId: string; // Changed from 'category' to 'categoryId'
  type: 'speaking' | 'listening' | 'reading' | 'writing' | 'general';
  difficulty: 'beginner' | 'intermediate' | 'advanced';
  level: number; // User level (1-10)
  tags: string[];
  image?: string; // URL to uploaded image
}

const initialFormData: TipFormData = {
  title: '',
  content: '',
  categoryId: '',
  type: 'general',
  difficulty: 'intermediate',
  level: 1,
  tags: [],
  image: '',
};

export const Tips: React.FC = () => {
  const [openDialog, setOpenDialog] = useState(false);
  const [editingTip, setEditingTip] = useState<Tip | null>(null);
  const [formData, setFormData] = useState<TipFormData>(initialFormData);
  const [error, setError] = useState('');
  const [tagInput, setTagInput] = useState('');
  const [statusFilter, setStatusFilter] = useState<'active' | 'inactive' | 'all'>('active');
  const [uploadingImage, setUploadingImage] = useState(false);
  const [imagePreview, setImagePreview] = useState<string | null>(null);

  const queryClient = useQueryClient();

  const { data: tipsResponse, isLoading: tipsLoading } = useQuery({
    queryKey: ['tips', statusFilter],
    queryFn: () => apiService.getTips({ status: statusFilter }),
  });

  const { data: categoriesResponse, isLoading: categoriesLoading } = useQuery({
    queryKey: ['categories'],
    queryFn: () => apiService.getCategories(),
  });

  const createMutation = useMutation({
    mutationFn: (data: TipFormData) => apiService.createTip(data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['tips'] });
      handleCloseDialog();
    },
    onError: (error: any) => {
      setError(error.response?.data?.message || 'Failed to create tip');
    },
  });

  const updateMutation = useMutation({
    mutationFn: ({ id, data }: { id: string; data: TipFormData }) =>
      apiService.updateTip(id, data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['tips'] });
      handleCloseDialog();
    },
    onError: (error: any) => {
      setError(error.response?.data?.message || 'Failed to update tip');
    },
  });

  const deleteMutation = useMutation({
    mutationFn: (id: string) => apiService.deleteTip(id),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['tips'] });
    },
    onError: (error: any) => {
      setError(error.response?.data?.message || 'Failed to delete tip');
    },
  });

  const restoreMutation = useMutation({
    mutationFn: (id: string) => apiService.updateTip(id, { isActive: true }),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['tips'] });
    },
    onError: (error: any) => {
      setError(error.response?.data?.message || 'Failed to restore tip');
    },
  });

  const tips = tipsResponse?.data?.items || [];
  const categories = categoriesResponse?.data?.categories || [];

  const handleOpenDialog = (tip?: Tip) => {
    if (tip) {
      setEditingTip(tip);
      setFormData({
        title: tip.title,
        content: tip.content,
        categoryId: typeof tip.categoryId === 'string' ? tip.categoryId : 
                   typeof tip.category === 'string' ? tip.category : 
                   tip.category?._id || tip.categoryId?._id || '',
        type: tip.type,
        difficulty: tip.difficulty,
        level: tip.level || 1,
        tags: tip.tags || [],
        image: tip.image || '',
      });
      setImagePreview(tip.image || null);
    } else {
      setEditingTip(null);
      setFormData(initialFormData);
      setImagePreview(null);
    }
    setError('');
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
    setEditingTip(null);
    setFormData(initialFormData);
    setError('');
    setTagInput('');
    setImagePreview(null);
    setUploadingImage(false);
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (editingTip) {
      updateMutation.mutate({ id: editingTip._id, data: formData });
    } else {
      createMutation.mutate(formData);
    }
  };

  const handleImageUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    // Validate file type
    const allowedTypes = ['image/jpeg', 'image/png', 'image/gif'];
    if (!allowedTypes.includes(file.type)) {
      setError('Please select a valid image file (JPEG, PNG, or GIF)');
      return;
    }

    // Validate file size (5MB)
    if (file.size > 5 * 1024 * 1024) {
      setError('Image size must be less than 5MB');
      return;
    }

    try {
      setUploadingImage(true);
      setError('');

      // Create preview
      const reader = new FileReader();
      reader.onload = (e) => {
        setImagePreview(e.target?.result as string);
      };
      reader.readAsDataURL(file);

      // Upload to server
      const response = await apiService.uploadImage(file);
      if (response.success) {
        setFormData({ ...formData, image: response.data.url });
      } else {
        throw new Error(response.message || 'Upload failed');
      }
    } catch (error: any) {
      setError(error.message || 'Failed to upload image');
      setImagePreview(null);
    } finally {
      setUploadingImage(false);
    }
  };

  const handleRemoveImage = () => {
    setFormData({ ...formData, image: '' });
    setImagePreview(null);
  };

  const handleDelete = (id: string) => {
    if (window.confirm('Are you sure you want to delete this tip?')) {
      deleteMutation.mutate(id);
    }
  };

  const handleRestore = (id: string) => {
    if (window.confirm('Are you sure you want to restore this tip?')) {
      restoreMutation.mutate(id);
    }
  };

  const handleAddTag = () => {
    if (tagInput.trim() && !formData.tags.includes(tagInput.trim())) {
      setFormData({ ...formData, tags: [...formData.tags, tagInput.trim()] });
      setTagInput('');
    }
  };

  const handleRemoveTag = (tagToRemove: string) => {
    setFormData({
      ...formData,
      tags: formData.tags.filter(tag => tag !== tagToRemove),
    });
  };

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'beginner':
        return 'success';
      case 'intermediate':
        return 'warning';
      case 'advanced':
        return 'error';
      default:
        return 'default';
    }
  };

  const getTypeIcon = (type: string) => {
    switch (type) {
      case 'speaking':
        return '🗣️';
      case 'listening':
        return '👂';
      case 'reading':
        return '📖';
      case 'writing':
        return '✍️';
      case 'general':
        return '💡';
      default:
        return '💡';
    }
  };

  const getCategoryName = (tip: Tip) => {
    if (tip.categoryId && typeof tip.categoryId === 'object') {
      return tip.categoryId.name;
    }
    if (tip.category && typeof tip.category === 'object') {
      return tip.category.name;
    }
    return 'Unknown';
  };

  if (tipsLoading || categoriesLoading) {
    return (
      <div className="flex justify-center items-center min-h-[400px]">
        <CircularProgress />
      </div>
    );
  }

  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <Typography variant="h4">Tips</Typography>
        <Button
          variant="contained"
          startIcon={<AddIcon />}
          onClick={() => handleOpenDialog()}
        >
          Add Tip
        </Button>
      </div>

      <div className="flex justify-between items-center mb-6">
        <ToggleButtonGroup
          value={statusFilter}
          exclusive
          onChange={(e, newValue) => newValue && setStatusFilter(newValue)}
          size="small"
        >
          <ToggleButton value="active">Active</ToggleButton>
          <ToggleButton value="inactive">Deleted</ToggleButton>
          <ToggleButton value="all">All</ToggleButton>
        </ToggleButtonGroup>
        <Typography variant="body2" color="text.secondary">
          {tips.length} tips
        </Typography>
      </div>

      {error && (
        <Alert severity="error" className="mb-6">
          {error}
        </Alert>
      )}

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {tips.map((tip: Tip) => (
          <div key={tip._id} className={`bg-white rounded-lg shadow-md p-6 ${!tip.isActive ? 'opacity-60' : ''}`}>
            <div className="flex items-start justify-between mb-4">
              <div className="flex-1">
                <div className="flex items-center gap-2 mb-2">
                  <span className="text-lg">{getTypeIcon(tip.type)}</span>
                  <Typography variant="h6" className="flex-1">
                    {tip.title}
                  </Typography>
                </div>
                
                {/* Image Preview */}
                {tip.image && (
                  <div className="mb-3">
                    <img
                      src={tip.image}
                      alt={tip.title}
                      className="w-full h-32 object-cover rounded border"
                    />
                  </div>
                )}
                
                <div className="flex items-center gap-2 mb-3">
                  <Chip
                    label={getCategoryName(tip)}
                    size="small"
                    color="primary"
                    variant="outlined"
                  />
                  <Chip
                    label={tip.difficulty}
                    size="small"
                    color={getDifficultyColor(tip.difficulty) as any}
                  />
                  <Chip
                    label={tip.type}
                    size="small"
                    variant="outlined"
                  />
                  <Chip
                    label={`Level ${tip.level || 1}`}
                    size="small"
                    color="info"
                  />
                  {!tip.isActive && (
                    <Chip
                      label="Deleted"
                      size="small"
                      color="error"
                    />
                  )}
                </div>

                <Typography variant="body2" color="text.secondary" className="mb-3">
                  {tip.content}
                </Typography>

                {tip.tags && tip.tags.length > 0 && (
                  <div className="flex flex-wrap gap-1 mb-3">
                    {tip.tags.map((tag: string, index: number) => (
                      <Chip
                        key={index}
                        label={tag}
                        size="small"
                        variant="outlined"
                        color="secondary"
                      />
                    ))}
                  </div>
                )}
              </div>

              <div className="flex gap-1 ml-4">
                {tip.isActive ? (
                  <>
                    <IconButton
                      size="small"
                      onClick={() => handleOpenDialog(tip)}
                      color="primary"
                    >
                      <EditIcon />
                    </IconButton>
                    <IconButton
                      size="small"
                      onClick={() => handleDelete(tip._id)}
                      color="error"
                    >
                      <DeleteIcon />
                    </IconButton>
                  </>
                ) : (
                  <IconButton
                    size="small"
                    onClick={() => handleRestore(tip._id)}
                    color="success"
                  >
                    <RestoreIcon />
                  </IconButton>
                )}
              </div>
            </div>
          </div>
        ))}
      </div>

      <Dialog open={openDialog} onClose={handleCloseDialog} maxWidth="md" fullWidth>
        <DialogTitle>
          {editingTip ? 'Edit Tip' : 'Add New Tip'}
        </DialogTitle>
        <form onSubmit={handleSubmit}>
          <DialogContent>
            <TextField
              fullWidth
              label="Title"
              value={formData.title}
              onChange={(e) => setFormData({ ...formData, title: e.target.value })}
              margin="normal"
              required
            />

            <TextField
              fullWidth
              label="Content"
              value={formData.content}
              onChange={(e) => setFormData({ ...formData, content: e.target.value })}
              margin="normal"
              multiline
              rows={4}
              required
            />

            {/* Image Upload Section */}
            <div className="mt-4">
              <Typography variant="subtitle2" className="mb-2">Image:</Typography>
              
              {imagePreview || formData.image ? (
                <div className="mb-2">
                  <div className="relative inline-block">
                    <img
                      src={imagePreview || formData.image}
                      alt="Preview"
                      className="w-32 h-24 object-cover rounded border"
                    />
                    <IconButton
                      onClick={handleRemoveImage}
                      className="absolute -top-2 -right-2 bg-red-500 text-white hover:bg-red-600"
                      size="small"
                    >
                      <DeleteIcon fontSize="small" />
                    </IconButton>
                  </div>
                </div>
              ) : null}

              <div className="flex gap-2 items-center">
                <input
                  type="file"
                  accept="image/*"
                  onChange={handleImageUpload}
                  style={{ display: 'none' }}
                  id="image-upload"
                  disabled={uploadingImage}
                />
                <label htmlFor="image-upload">
                  <Button
                    variant="outlined"
                    component="span"
                    startIcon={uploadingImage ? <CircularProgress size={16} /> : <UploadIcon />}
                    disabled={uploadingImage}
                  >
                    {uploadingImage ? 'Uploading...' : 'Upload Image'}
                  </Button>
                </label>
                {(imagePreview || formData.image) && (
                  <Button
                    variant="text"
                    color="error"
                    startIcon={<DeleteIcon />}
                    onClick={handleRemoveImage}
                    size="small"
                  >
                    Remove
                  </Button>
                )}
              </div>
            </div>

            <FormControl fullWidth margin="normal">
              <InputLabel>Category</InputLabel>
              <Select
                value={formData.categoryId}
                onChange={(e) => setFormData({ ...formData, categoryId: e.target.value })}
                label="Category"
                required
              >
                {categories.map((category: Category) => (
                  <MenuItem key={category._id} value={category._id}>
                    {category.name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>

            <FormControl fullWidth margin="normal">
              <InputLabel>Type</InputLabel>
              <Select
                value={formData.type}
                onChange={(e) => setFormData({ ...formData, type: e.target.value as any })}
                label="Type"
              >
                <MenuItem value="speaking">Speaking</MenuItem>
                <MenuItem value="listening">Listening</MenuItem>
                <MenuItem value="reading">Reading</MenuItem>
                <MenuItem value="writing">Writing</MenuItem>
                <MenuItem value="general">General</MenuItem>
              </Select>
            </FormControl>

            <FormControl fullWidth margin="normal">
              <InputLabel>Difficulty</InputLabel>
              <Select
                value={formData.difficulty}
                onChange={(e) => setFormData({ ...formData, difficulty: e.target.value as any })}
                label="Difficulty"
              >
                <MenuItem value="beginner">Beginner</MenuItem>
                <MenuItem value="intermediate">Intermediate</MenuItem>
                <MenuItem value="advanced">Advanced</MenuItem>
              </Select>
            </FormControl>

            <FormControl fullWidth margin="normal">
              <InputLabel>Level</InputLabel>
              <Select
                value={formData.level}
                onChange={(e) => setFormData({ ...formData, level: Number(e.target.value) })}
                label="Level"
              >
                <MenuItem value={1}>1 - Beginner</MenuItem>
                <MenuItem value={2}>2 - Elementary</MenuItem>
                <MenuItem value={3}>3 - Pre-Intermediate</MenuItem>
                <MenuItem value={4}>4 - Intermediate</MenuItem>
                <MenuItem value={5}>5 - Upper-Intermediate</MenuItem>
                <MenuItem value={6}>6 - Advanced</MenuItem>
                <MenuItem value={7}>7 - Upper-Advanced</MenuItem>
                <MenuItem value={8}>8 - Expert</MenuItem>
                <MenuItem value={9}>9 - Master</MenuItem>
                <MenuItem value={10}>10 - Grand Master</MenuItem>
              </Select>
            </FormControl>

            <div className="mt-4">
              <Typography variant="subtitle2" className="mb-2">Tags:</Typography>
              <div className="flex gap-2 mb-2">
                <TextField
                  size="small"
                  placeholder="Add a tag"
                  value={tagInput}
                  onChange={(e) => setTagInput(e.target.value)}
                  onKeyPress={(e) => e.key === 'Enter' && (e.preventDefault(), handleAddTag())}
                />
                <Button
                  variant="outlined"
                  size="small"
                  onClick={handleAddTag}
                  disabled={!tagInput.trim()}
                >
                  Add
                </Button>
              </div>
              <div className="flex flex-wrap gap-1">
                {formData.tags.map((tag: string, index: number) => (
                  <Chip
                    key={index}
                    label={tag}
                    size="small"
                    onDelete={() => handleRemoveTag(tag)}
                    color="secondary"
                  />
                ))}
              </div>
            </div>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleCloseDialog}>Cancel</Button>
            <Button
              type="submit"
              variant="contained"
              disabled={createMutation.isPending || updateMutation.isPending}
            >
              {createMutation.isPending || updateMutation.isPending ? (
                <CircularProgress size={20} />
              ) : editingTip ? (
                'Update'
              ) : (
                'Create'
              )}
            </Button>
          </DialogActions>
        </form>
      </Dialog>
    </div>
  );
}; 