import React, { useState } from 'react';
import {
  Box,
  Typography,
  Card,
  CardContent,
  Button,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Alert,
  CircularProgress,
  Chip,
  Avatar,
  Tooltip,
  Divider,
  Grid,
  Paper,
  Badge,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  ListItemSecondaryAction,
  Switch,
  FormControlLabel,
  SelectChangeEvent,
  Pagination,
  Stack,
} from '@mui/material';
import {
  Notifications as NotificationsIcon,
  Send as SendIcon,
  Delete as DeleteIcon,
  MarkEmailRead as MarkReadIcon,
  MarkEmailUnread as MarkUnreadIcon,
  Add as AddIcon,
  FilterList as FilterIcon,
  Refresh as RefreshIcon,
  Info as InfoIcon,
  Warning as WarningIcon,
  CheckCircle as CheckCircleIcon,
  School as SchoolIcon,
  TrendingUp as TrendingUpIcon,
  Schedule as ScheduleIcon,
} from '@mui/icons-material';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { apiService } from '../services/api';
import { Notification, NotificationFormData, NotificationStats } from '../types';

interface NotificationFormDataExtended extends NotificationFormData {
  sendToAll?: boolean;
  selectedUserIds?: string[];
}

const initialFormData: NotificationFormDataExtended = {
  title: '',
  message: '',
  type: 'system',
  sendToAll: false,
  selectedUserIds: [],
};

const getNotificationIcon = (type: string) => {
  switch (type) {
    case 'tip_unlocked':
      return <SchoolIcon />;
    case 'quiz_reminder':
      return <ScheduleIcon />;
    case 'daily_reminder':
      return <TrendingUpIcon />;
    case 'achievement':
      return <CheckCircleIcon />;
    case 'system':
      return <InfoIcon />;
    default:
      return <NotificationsIcon />;
  }
};

const getNotificationColor = (type: string) => {
  switch (type) {
    case 'tip_unlocked':
      return '#4caf50';
    case 'quiz_reminder':
      return '#2196f3';
    case 'daily_reminder':
      return '#ff9800';
    case 'achievement':
      return '#9c27b0';
    case 'system':
      return '#607d8b';
    default:
      return '#757575';
  }
};

const getNotificationTypeLabel = (type: string) => {
  switch (type) {
    case 'tip_unlocked':
      return 'Tip Unlocked';
    case 'quiz_reminder':
      return 'Quiz Reminder';
    case 'daily_reminder':
      return 'Daily Reminder';
    case 'achievement':
      return 'Achievement';
    case 'system':
      return 'System';
    default:
      return type;
  }
};

export const Notifications: React.FC = () => {
  const [openDialog, setOpenDialog] = useState(false);
  const [formData, setFormData] = useState<NotificationFormDataExtended>(initialFormData);
  const [error, setError] = useState('');
  const [filters, setFilters] = useState({
    type: '',
    isRead: undefined as boolean | undefined,
    page: 1,
    limit: 20,
  });

  const queryClient = useQueryClient();

  // Fetch notifications with filters
  const { data: notificationsResponse, isLoading } = useQuery({
    queryKey: ['notifications', filters],
    queryFn: () => apiService.getNotifications(filters),
  });

  // Fetch notification stats
  const { data: statsResponse } = useQuery({
    queryKey: ['notification-stats'],
    queryFn: () => apiService.getNotificationStats(),
  });

  // Send notification mutation
  const sendMutation = useMutation({
    mutationFn: (data: NotificationFormData) => apiService.sendNotification(data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['notifications'] });
      queryClient.invalidateQueries({ queryKey: ['notification-stats'] });
      handleCloseDialog();
    },
    onError: (error: any) => {
      setError(error.response?.data?.message || 'Failed to send notification');
    },
  });

  // Delete notification mutation
  const deleteMutation = useMutation({
    mutationFn: (id: string) => apiService.deleteNotification(id),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['notifications'] });
      queryClient.invalidateQueries({ queryKey: ['notification-stats'] });
    },
    onError: (error: any) => {
      setError(error.response?.data?.message || 'Failed to delete notification');
    },
  });

  // Mark as read mutation
  const markReadMutation = useMutation({
    mutationFn: (id: string) => apiService.markNotificationAsRead(id),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['notifications'] });
      queryClient.invalidateQueries({ queryKey: ['notification-stats'] });
    },
    onError: (error: any) => {
      setError(error.response?.data?.message || 'Failed to mark notification as read');
    },
  });

  // Mark all as read mutation
  const markAllReadMutation = useMutation({
    mutationFn: () => apiService.markAllNotificationsAsRead(),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['notifications'] });
      queryClient.invalidateQueries({ queryKey: ['notification-stats'] });
    },
    onError: (error: any) => {
      setError(error.response?.data?.message || 'Failed to mark all notifications as read');
    },
  });

  const notifications = notificationsResponse?.data?.items || [];
  const stats = statsResponse?.data || {
    total: 0,
    unread: 0,
    read: 0,
    byType: {},
  };

  const handleOpenDialog = () => {
    setFormData(initialFormData);
    setError('');
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
    setFormData(initialFormData);
    setError('');
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const { sendToAll, selectedUserIds, ...notificationData } = formData;
    sendMutation.mutate(notificationData);
  };

  const handleDelete = (id: string) => {
    if (window.confirm('Are you sure you want to delete this notification?')) {
      deleteMutation.mutate(id);
    }
  };

  const handleMarkAsRead = (id: string) => {
    markReadMutation.mutate(id);
  };

  const handleMarkAllAsRead = () => {
    markAllReadMutation.mutate();
  };

  const handleFilterChange = (field: string, value: string) => {
    setFilters(prev => ({
      ...prev,
      [field]: field === 'isRead' ? (value === '' ? undefined : value === 'true') : value,
      page: 1, // Reset to first page when filtering
    }));
  };

  const handlePageChange = (event: React.ChangeEvent<unknown>, page: number) => {
    setFilters(prev => ({
      ...prev,
      page,
    }));
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  const StatCard: React.FC<{
    title: string;
    value: number;
    icon: React.ReactNode;
    color: string;
  }> = ({ title, value, icon, color }) => (
    <div className="bg-white rounded-lg shadow-md p-6 h-full">
      <div className="flex items-center justify-between">
        <div>
          <Typography color="textSecondary" gutterBottom variant="h6">
            {title}
          </Typography>
          <Typography variant="h4" component="div">
            {value.toLocaleString()}
          </Typography>
        </div>
        <div
          className="rounded-full p-3 flex items-center justify-center"
          style={{ backgroundColor: color }}
        >
          {icon}
        </div>
      </div>
    </div>
  );

  return (
    <div>
      {/* Header */}
      <div className="mb-6 flex justify-between items-start">
        <div>
          <Typography variant="h4" className="mb-2">
            Notifications
          </Typography>
          <Typography variant="body2" color="textSecondary">
            Manage and send notifications to users
          </Typography>
        </div>
        <div className="flex gap-2">
          <Tooltip title="Mark all as read">
            <IconButton
              onClick={handleMarkAllAsRead}
              disabled={markAllReadMutation.isPending || stats.unread === 0}
              color="primary"
            >
              <MarkReadIcon />
            </IconButton>
          </Tooltip>
          <Button
            variant="contained"
            startIcon={<AddIcon />}
            onClick={handleOpenDialog}
            disabled={sendMutation.isPending}
          >
            Send Notification
          </Button>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6 mb-6">
        <StatCard
          title="Total Notifications"
          value={stats.total}
          icon={<NotificationsIcon />}
          color="#1976d2"
        />
        <StatCard
          title="Unread"
          value={stats.unread}
          icon={<MarkUnreadIcon />}
          color="#f57c00"
        />
        <StatCard
          title="Read"
          value={stats.read}
          icon={<MarkReadIcon />}
          color="#388e3c"
        />
        <StatCard
          title="Pending"
          value={notifications.filter(n => !n.isSent).length}
          icon={<ScheduleIcon />}
          color="#7b1fa2"
        />
      </div>

      {/* Filters */}
      <div className="bg-white rounded-lg shadow-md p-4 mb-6">
        <div className="flex flex-wrap gap-4 items-center">
          <Typography variant="h6" className="mr-4">
            <FilterIcon className="mr-2" />
            Filters
          </Typography>
          
          <FormControl size="small" className="min-w-[150px]">
            <InputLabel>Type</InputLabel>
            <Select
              value={filters.type}
              label="Type"
              onChange={(e: SelectChangeEvent) => handleFilterChange('type', e.target.value)}
            >
              <MenuItem value="">All Types</MenuItem>
              <MenuItem value="tip_unlocked">Tip Unlocked</MenuItem>
              <MenuItem value="quiz_reminder">Quiz Reminder</MenuItem>
              <MenuItem value="daily_reminder">Daily Reminder</MenuItem>
              <MenuItem value="achievement">Achievement</MenuItem>
              <MenuItem value="system">System</MenuItem>
            </Select>
          </FormControl>

                     <FormControl size="small" className="min-w-[150px]">
             <InputLabel>Status</InputLabel>
             <Select
               value={filters.isRead === undefined ? '' : filters.isRead.toString()}
               label="Status"
               onChange={(e: SelectChangeEvent) => handleFilterChange('isRead', e.target.value)}
             >
               <MenuItem value="">All</MenuItem>
               <MenuItem value="false">Unread</MenuItem>
               <MenuItem value="true">Read</MenuItem>
             </Select>
           </FormControl>

          <Tooltip title="Refresh">
            <IconButton
              onClick={() => queryClient.invalidateQueries({ queryKey: ['notifications'] })}
              disabled={isLoading}
            >
              <RefreshIcon />
            </IconButton>
          </Tooltip>
        </div>
      </div>

            {/* Notifications List */}
      {isLoading ? (
        <div className="flex justify-center items-center min-h-[400px]">
          <CircularProgress />
        </div>
      ) : error ? (
        <Alert severity="error" className="mb-4">
          {error}
        </Alert>
      ) : notifications.length === 0 ? (
        <div className="bg-white rounded-lg shadow-md">
          <div className="text-center py-8">
            <NotificationsIcon className="text-6xl text-gray-400 mb-4" />
            <Typography variant="h6" color="textSecondary" gutterBottom>
              No notifications found
            </Typography>
                         <Typography variant="body2" color="textSecondary">
               {filters.type || filters.isRead !== undefined ? 'Try adjusting your filters' : 'Start by sending your first notification'}
             </Typography>
          </div>
        </div>
      ) : (
        <div className="bg-white rounded-lg shadow-md">
          <div className="divide-y divide-gray-200">
            {notifications.map((notification, index) => (
              <div key={notification._id} className="p-4 hover:bg-gray-50">
                <div className="flex items-start space-x-4">
                  <div
                    className="rounded-full p-2 flex items-center justify-center"
                    style={{ backgroundColor: getNotificationColor(notification.type) }}
                  >
                    {getNotificationIcon(notification.type)}
                  </div>
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center gap-2 mb-2">
                      <Typography
                        component="span"
                        variant="h6"
                        color="text.primary"
                        className={notification.isRead ? '' : 'font-semibold'}
                      >
                        {notification.title}
                      </Typography>
                      <Chip
                        label={getNotificationTypeLabel(notification.type)}
                        size="small"
                        style={{
                          backgroundColor: getNotificationColor(notification.type),
                          color: 'white',
                        }}
                      />
                      {!notification.isRead && (
                        <Chip
                          label="Unread"
                          size="small"
                          color="warning"
                          variant="outlined"
                        />
                      )}
                    </div>
                    <Typography
                      component="span"
                      variant="body2"
                      color="text.primary"
                      className={notification.isRead ? '' : 'font-medium'}
                    >
                      {notification.message}
                    </Typography>
                    <div className="flex items-center gap-4 mt-2 text-sm text-gray-500">
                      <span>Created: {formatDate(notification.createdAt.toString())}</span>
                      {notification.sentAt && (
                        <span>Sent: {formatDate(notification.sentAt.toString())}</span>
                      )}
                      {notification.readAt && (
                        <span>Read: {formatDate(notification.readAt.toString())}</span>
                      )}
                    </div>
                  </div>
                  <div className="flex gap-1">
                    {!notification.isRead && (
                      <Tooltip title="Mark as read">
                        <IconButton
                          onClick={() => handleMarkAsRead(notification._id)}
                          disabled={markReadMutation.isPending}
                          color="primary"
                          size="small"
                        >
                          <MarkReadIcon />
                        </IconButton>
                      </Tooltip>
                    )}
                    <Tooltip title="Delete">
                      <IconButton
                        onClick={() => handleDelete(notification._id)}
                        disabled={deleteMutation.isPending}
                        color="error"
                        size="small"
                      >
                        <DeleteIcon />
                      </IconButton>
                    </Tooltip>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

       {/* Pagination */}
       {notificationsResponse?.data?.totalPages && notificationsResponse.data.totalPages > 1 && (
         <div className="flex justify-center mt-6">
           <Pagination
             count={notificationsResponse.data.totalPages}
             page={filters.page}
             onChange={handlePageChange}
             color="primary"
             showFirstButton
             showLastButton
           />
         </div>
       )}

               {/* Send Notification Dialog */}
       <Dialog open={openDialog} onClose={handleCloseDialog} maxWidth="md" fullWidth>
         <DialogTitle>
           <div className="flex items-center gap-2">
             <SendIcon />
             Send Notification
           </div>
         </DialogTitle>
        <form onSubmit={handleSubmit}>
          <DialogContent>
            {error && (
              <Alert severity="error" className="mb-4">
                {error}
              </Alert>
            )}
            
                         <div className="grid grid-cols-1 gap-4">
               <TextField
                 fullWidth
                 label="Title"
                 value={formData.title}
                 onChange={(e) => setFormData({ ...formData, title: e.target.value })}
                 required
                 variant="outlined"
               />
               
               <TextField
                 fullWidth
                 label="Message"
                 value={formData.message}
                 onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                 required
                 multiline
                 rows={4}
                 variant="outlined"
               />
               
               <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                 <FormControl fullWidth>
                   <InputLabel>Type</InputLabel>
                   <Select
                     value={formData.type}
                     label="Type"
                     onChange={(e) => setFormData({ ...formData, type: e.target.value as any })}
                     required
                   >
                     <MenuItem value="tip_unlocked">Tip Unlocked</MenuItem>
                     <MenuItem value="quiz_reminder">Quiz Reminder</MenuItem>
                     <MenuItem value="daily_reminder">Daily Reminder</MenuItem>
                     <MenuItem value="achievement">Achievement</MenuItem>
                     <MenuItem value="system">System</MenuItem>
                   </Select>
                 </FormControl>
               </div>
               
               <FormControlLabel
                 control={
                   <Switch
                     checked={formData.sendToAll}
                     onChange={(e) => setFormData({ ...formData, sendToAll: e.target.checked })}
                   />
                 }
                 label="Send to all users"
               />
             </div>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleCloseDialog}>Cancel</Button>
            <Button
              type="submit"
              variant="contained"
              startIcon={<SendIcon />}
              disabled={sendMutation.isPending}
            >
              {sendMutation.isPending ? 'Sending...' : 'Send Notification'}
            </Button>
          </DialogActions>
        </form>
      </Dialog>
    </div>
  );
}; 