import React, { useState } from 'react';
import {
  Button,
  Typography,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Alert,
  CircularProgress,
  Chip,
  ToggleButtonGroup,
  ToggleButton,
} from '@mui/material';
import {
  Add as AddIcon,
  Edit as EditIcon,
  Delete as DeleteIcon,
  RestoreFromTrash as RestoreIcon,
  QuestionAnswer,
} from '@mui/icons-material';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { apiService } from '../services/api';
import { Question, Category, QuestionOption } from '../types';

interface QuestionFormData {
  question: string; // Changed from 'text' to 'question'
  categoryId: string; // Changed from 'category' to 'categoryId'
  type: 'multiple_choice' | 'true_false' | 'fill_blank' | 'matching' | 'ordering';
  options?: QuestionOption[];
  correctAnswer: string; // Changed from number to string
  explanation?: string;
  difficulty: 'easy' | 'medium' | 'hard';
  points: number;
}

const initialFormData: QuestionFormData = {
  question: '',
  categoryId: '',
  type: 'multiple_choice',
  options: [
    { id: 'A', text: '' },
    { id: 'B', text: '' },
    { id: 'C', text: '' },
    { id: 'D', text: '' }
  ],
  correctAnswer: '',
  explanation: '',
  difficulty: 'medium',
  points: 10,
};

export const Questions: React.FC = () => {
  const [openDialog, setOpenDialog] = useState(false);
  const [editingQuestion, setEditingQuestion] = useState<Question | null>(null);
  const [formData, setFormData] = useState<QuestionFormData>(initialFormData);
  const [error, setError] = useState('');
  const [statusFilter, setStatusFilter] = useState<'active' | 'inactive' | 'all'>('active');

  const queryClient = useQueryClient();

  const { data: questionsResponse, isLoading: questionsLoading } = useQuery({
    queryKey: ['questions', statusFilter],
    queryFn: () => apiService.getQuestions({ status: statusFilter }),
  });

  const { data: categoriesResponse, isLoading: categoriesLoading } = useQuery({
    queryKey: ['categories'],
    queryFn: () => apiService.getCategories(),
  });

  const createMutation = useMutation({
    mutationFn: (data: QuestionFormData) => apiService.createQuestion(data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['questions'] });
      handleCloseDialog();
    },
    onError: (error: any) => {
      setError(error.response?.data?.message || 'Failed to create question');
    },
  });

  const updateMutation = useMutation({
    mutationFn: ({ id, data }: { id: string; data: QuestionFormData }) =>
      apiService.updateQuestion(id, data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['questions'] });
      handleCloseDialog();
    },
    onError: (error: any) => {
      setError(error.response?.data?.message || 'Failed to update question');
    },
  });

  const deleteMutation = useMutation({
    mutationFn: (id: string) => apiService.deleteQuestion(id),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['questions'] });
    },
    onError: (error: any) => {
      setError(error.response?.data?.message || 'Failed to delete question');
    },
  });

  const restoreMutation = useMutation({
    mutationFn: (id: string) => apiService.updateQuestion(id, { isActive: true }),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['questions'] });
    },
    onError: (error: any) => {
      setError(error.response?.data?.message || 'Failed to restore question');
    },
  });

  const questions = questionsResponse?.data?.items || [];
  const categories = categoriesResponse?.data?.categories || [];

  const handleOpenDialog = (question?: Question) => {
    if (question) {
      setEditingQuestion(question);
      
      // Convert options to new format if needed
      let options: QuestionOption[] = [];
      if (question.options && Array.isArray(question.options) && question.options.length > 0) {
        if (typeof question.options[0] === 'string') {
          // Convert old string format to new object format
          options = (question.options as string[]).map((option, index) => ({
            id: String.fromCharCode(65 + index),
            text: option
          }));
        } else {
          // Already in new format
          options = question.options as QuestionOption[];
        }
      }
      
      setFormData({
        question: question.question || question.text || '', // Handle both field names
        categoryId: typeof question.categoryId === 'string' ? question.categoryId : 
                   typeof question.category === 'string' ? question.category : 
                   question.category?._id || question.categoryId?._id || '',
        type: question.type,
        options: options,
        correctAnswer: question.correctAnswer?.toString() || '',
        explanation: question.explanation || '',
        difficulty: question.difficulty,
        points: question.points,
      });
    } else {
      setEditingQuestion(null);
      setFormData(initialFormData);
    }
    setError('');
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
    setEditingQuestion(null);
    setFormData(initialFormData);
    setError('');
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (editingQuestion) {
      updateMutation.mutate({ id: editingQuestion._id, data: formData });
    } else {
      createMutation.mutate(formData);
    }
  };

  const handleDelete = (id: string) => {
    if (window.confirm('Are you sure you want to delete this question?')) {
      deleteMutation.mutate(id);
    }
  };

  const handleRestore = (id: string) => {
    if (window.confirm('Are you sure you want to restore this question?')) {
      restoreMutation.mutate(id);
    }
  };

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'easy':
        return 'success';
      case 'medium':
        return 'warning';
      case 'hard':
        return 'error';
      default:
        return 'default';
    }
  };

  const getTypeIcon = (type: string) => {
    switch (type) {
      case 'multiple_choice':
        return '📝';
      case 'true_false':
        return '✅❌';
      case 'fill_blank':
        return '📝';
      case 'matching':
        return '🔗';
      case 'ordering':
        return '📋';
      default:
        return '❓';
    }
  };

  const getCategoryName = (question: Question) => {
    if (question.categoryId && typeof question.categoryId === 'object') {
      return question.categoryId.name;
    }
    if (question.category && typeof question.category === 'object') {
      return question.category.name;
    }
    return 'Unknown';
  };

  const getQuestionText = (question: Question) => {
    return question.question || question.text || 'No question text';
  };

  const getOptionText = (option: QuestionOption | string, index: number) => {
    if (typeof option === 'string') {
      return option;
    }
    return option.text;
  };

  const getOptionId = (option: QuestionOption | string, index: number) => {
    if (typeof option === 'string') {
      return String.fromCharCode(65 + index);
    }
    return option.id;
  };

  const isCorrectAnswer = (option: QuestionOption | string, question: Question, index: number) => {
    const optionId = getOptionId(option, index);
    return optionId === question.correctAnswer;
  };

  if (questionsLoading || categoriesLoading) {
    return (
      <div className="flex justify-center items-center min-h-[400px]">
        <CircularProgress />
      </div>
    );
  }

  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <Typography variant="h4">Questions</Typography>
        <Button
          variant="contained"
          startIcon={<AddIcon />}
          onClick={() => handleOpenDialog()}
        >
          Add Question
        </Button>
      </div>

      <div className="flex justify-between items-center mb-6">
        <ToggleButtonGroup
          value={statusFilter}
          exclusive
          onChange={(e, newValue) => newValue && setStatusFilter(newValue)}
          size="small"
        >
          <ToggleButton value="active">Active</ToggleButton>
          <ToggleButton value="inactive">Deleted</ToggleButton>
          <ToggleButton value="all">All</ToggleButton>
        </ToggleButtonGroup>
        <Typography variant="body2" color="text.secondary">
          {questions.length} questions
        </Typography>
      </div>

      {error && (
        <Alert severity="error" className="mb-6">
          {error}
        </Alert>
      )}

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {questions.map((question: Question) => (
          <div key={question._id} className={`bg-white rounded-lg shadow-md p-6 ${!question.isActive ? 'opacity-60' : ''}`}>
            <div className="flex items-start justify-between mb-4">
              <div className="flex-1">
                <div className="flex items-center gap-2 mb-2">
                  <span className="text-lg">{getTypeIcon(question.type)}</span>
                  <Typography variant="h6" className="flex-1">
                    {getQuestionText(question)}
                  </Typography>
                </div>
                
                <div className="flex items-center gap-2 mb-3">
                  <Chip
                    label={getCategoryName(question)}
                    size="small"
                    color="primary"
                    variant="outlined"
                  />
                  <Chip
                    label={question.difficulty}
                    size="small"
                    color={getDifficultyColor(question.difficulty) as any}
                  />
                  <Chip
                    label={`${question.points} pts`}
                    size="small"
                    variant="outlined"
                  />
                  {!question.isActive && (
                    <Chip
                      label="Deleted"
                      size="small"
                      color="error"
                    />
                  )}
                </div>

                {question.type === 'multiple_choice' && question.options && (
                  <div className="mb-3">
                    <Typography variant="body2" color="text.secondary" className="mb-2">
                      Options:
                    </Typography>
                    <div className="space-y-1">
                      {question.options.map((option: QuestionOption | string, index: number) => (
                        <div
                          key={index}
                          className={`p-2 rounded text-sm ${
                            isCorrectAnswer(option, question, index)
                              ? 'bg-green-100 text-green-800'
                              : 'bg-gray-50'
                          }`}
                        >
                          {getOptionId(option, index)}. {getOptionText(option, index)}
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {question.explanation && (
                  <Typography variant="body2" color="text.secondary" className="mb-3">
                    <strong>Explanation:</strong> {question.explanation}
                  </Typography>
                )}
              </div>

              <div className="flex gap-1 ml-4">
                {question.isActive ? (
                  <>
                    <IconButton
                      size="small"
                      onClick={() => handleOpenDialog(question)}
                      color="primary"
                    >
                      <EditIcon />
                    </IconButton>
                    <IconButton
                      size="small"
                      onClick={() => handleDelete(question._id)}
                      color="error"
                    >
                      <DeleteIcon />
                    </IconButton>
                  </>
                ) : (
                  <IconButton
                    size="small"
                    onClick={() => handleRestore(question._id)}
                    color="success"
                  >
                    <RestoreIcon />
                  </IconButton>
                )}
              </div>
            </div>
          </div>
        ))}
      </div>

      <Dialog open={openDialog} onClose={handleCloseDialog} maxWidth="md" fullWidth>
        <DialogTitle>
          {editingQuestion ? 'Edit Question' : 'Add New Question'}
        </DialogTitle>
        <form onSubmit={handleSubmit}>
          <DialogContent>
            <TextField
              fullWidth
              label="Question Text"
              value={formData.question}
              onChange={(e) => setFormData({ ...formData, question: e.target.value })}
              margin="normal"
              multiline
              rows={3}
              required
            />

            <FormControl fullWidth margin="normal">
              <InputLabel>Category</InputLabel>
              <Select
                value={formData.categoryId}
                onChange={(e) => setFormData({ ...formData, categoryId: e.target.value })}
                label="Category"
                required
              >
                {categories.map((category: Category) => (
                  <MenuItem key={category._id} value={category._id}>
                    {category.name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>

            <FormControl fullWidth margin="normal">
              <InputLabel>Type</InputLabel>
              <Select
                value={formData.type}
                onChange={(e) => setFormData({ ...formData, type: e.target.value as any })}
                label="Type"
              >
                <MenuItem value="multiple_choice">Multiple Choice</MenuItem>
                <MenuItem value="true_false">True/False</MenuItem>
                <MenuItem value="fill_blank">Fill in the Blank</MenuItem>
                <MenuItem value="matching">Matching</MenuItem>
                <MenuItem value="ordering">Ordering</MenuItem>
              </Select>
            </FormControl>

            {formData.type === 'multiple_choice' && (
              <div className="space-y-2 mt-4">
                <Typography variant="subtitle2">Options:</Typography>
                {formData.options?.map((option: QuestionOption, index: number) => (
                  <TextField
                    key={index}
                    fullWidth
                    label={`Option ${option.id}`}
                    value={option.text}
                    onChange={(e) => {
                      const newOptions = [...(formData.options || [])];
                      newOptions[index] = { ...option, text: e.target.value };
                      setFormData({ ...formData, options: newOptions });
                    }}
                    size="small"
                  />
                ))}
              </div>
            )}

            <TextField
              fullWidth
              label="Correct Answer (Option ID: A, B, C, D, etc.)"
              value={formData.correctAnswer}
              onChange={(e) => setFormData({ ...formData, correctAnswer: e.target.value })}
              margin="normal"
              required
              helperText="Enter the option ID (A, B, C, D) that is correct"
            />

            <TextField
              fullWidth
              label="Explanation (Optional)"
              value={formData.explanation}
              onChange={(e) => setFormData({ ...formData, explanation: e.target.value })}
              margin="normal"
              multiline
              rows={2}
            />

            <FormControl fullWidth margin="normal">
              <InputLabel>Difficulty</InputLabel>
              <Select
                value={formData.difficulty}
                onChange={(e) => setFormData({ ...formData, difficulty: e.target.value as any })}
                label="Difficulty"
              >
                <MenuItem value="easy">Easy</MenuItem>
                <MenuItem value="medium">Medium</MenuItem>
                <MenuItem value="hard">Hard</MenuItem>
              </Select>
            </FormControl>

            <TextField
              fullWidth
              label="Points"
              type="number"
              value={formData.points}
              onChange={(e) => setFormData({ ...formData, points: parseInt(e.target.value) })}
              margin="normal"
              required
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleCloseDialog}>Cancel</Button>
            <Button
              type="submit"
              variant="contained"
              disabled={createMutation.isPending || updateMutation.isPending}
            >
              {createMutation.isPending || updateMutation.isPending ? (
                <CircularProgress size={20} />
              ) : editingQuestion ? (
                'Update'
              ) : (
                'Create'
              )}
            </Button>
          </DialogActions>
        </form>
      </Dialog>
    </div>
  );
}; 