import React from 'react';
import {
  Typography,
  CircularProgress,
  IconButton,
  Tooltip,
} from '@mui/material';
import {
  People,
  QuestionAnswer,
  Lightbulb,
  Category,
  Assessment,
  TrendingUp,
  Refresh,
} from '@mui/icons-material';
import { useQuery } from '@tanstack/react-query';
import { apiService } from '../services/api';

const StatCard: React.FC<{
  title: string;
  value: number;
  icon: React.ReactNode;
  color: string;
}> = ({ title, value, icon, color }) => (
  <div className="bg-white rounded-lg shadow-md p-6 h-full">
    <div className="flex items-center justify-between">
      <div>
        <Typography color="textSecondary" gutterBottom variant="h6">
          {title}
        </Typography>
        <Typography variant="h4" component="div">
          {(value || 0).toLocaleString()}
        </Typography>
      </div>
      <div
        className="rounded-full p-3 flex items-center justify-center"
        style={{ backgroundColor: color }}
      >
        {icon}
      </div>
    </div>
  </div>
);

export const Dashboard: React.FC = () => {
  const { data: stats, isLoading, error, refetch } = useQuery({
    queryKey: ['dashboard-stats'],
    queryFn: () => apiService.getDashboardStats(),
    refetchInterval: 30000, // Refetch every 30 seconds
  });

  if (isLoading) {
    return (
      <div className="flex justify-center items-center min-h-[400px]">
        <CircularProgress />
      </div>
    );
  }

  if (error) {
    return (
      <div className="text-center p-8">
        <Typography color="error" variant="h6" className="mb-4">
          Error loading dashboard data
        </Typography>
        <Typography color="textSecondary" variant="body2">
          Please check your connection and try again
        </Typography>
      </div>
    );
  }

  const dashboardStats = stats?.data || {
    totalUsers: 0,
    totalQuestions: 0,
    totalTips: 0,
    totalCategories: 0,
    totalQuizResults: 0,
    averageScore: 0,
  };

  return (
    <div>
      <div className="mb-6 flex justify-between items-start">
        <div>
          <Typography variant="h4" className="mb-2">
            Dashboard
          </Typography>
          <Typography variant="body2" color="textSecondary">
            Welcome to SpeakCraft Admin Panel • Last updated: {new Date().toLocaleTimeString()}
          </Typography>
        </div>
        <Tooltip title="Refresh dashboard data">
          <IconButton 
            onClick={() => refetch()} 
            disabled={isLoading}
            className="bg-blue-50 hover:bg-blue-100"
          >
            {isLoading ? <CircularProgress size={20} /> : <Refresh />}
          </IconButton>
        </Tooltip>
      </div>
      
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          <StatCard
            title="Total Users"
            value={dashboardStats.totalUsers || 0}
            icon={<People className="text-white" />}
            color="#1976d2"
          />
          <StatCard
            title="Total Questions"
            value={dashboardStats.totalQuestions || 0}
            icon={<QuestionAnswer className="text-white" />}
            color="#388e3c"
          />
          <StatCard
            title="Total Tips"
            value={dashboardStats.totalTips || 0}
            icon={<Lightbulb className="text-white" />}
            color="#f57c00"
          />
          <StatCard
            title="Categories"
            value={dashboardStats.totalCategories || 0}
            icon={<Category className="text-white" />}
            color="#7b1fa2"
          />
          <StatCard
            title="Quiz Results"
            value={dashboardStats.totalQuizResults || 0}
            icon={<Assessment className="text-white" />}
            color="#d32f2f"
          />
          <StatCard
            title="Average Score"
            value={Math.round(dashboardStats.averageScore || 0)}
            icon={<TrendingUp className="text-white" />}
            color="#388e3c"
          />
        </div>

      {/* Summary Section */}
      <div className="mt-8">
        <Typography variant="h6" className="mb-4">
          Quick Insights
        </Typography>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          <div className="bg-blue-50 p-4 rounded-lg">
            <Typography variant="body2" color="textSecondary" className="mb-1">
              Active Users
            </Typography>
            <Typography variant="h6" color="primary">
              {dashboardStats.totalUsers || 0}
            </Typography>
          </div>
          <div className="bg-green-50 p-4 rounded-lg">
            <Typography variant="body2" color="textSecondary" className="mb-1">
              Total Content
            </Typography>
            <Typography variant="h6" color="primary">
              {(dashboardStats.totalQuestions || 0) + (dashboardStats.totalTips || 0)}
            </Typography>
          </div>
          <div className="bg-orange-50 p-4 rounded-lg">
            <Typography variant="body2" color="textSecondary" className="mb-1">
              Quiz Completion Rate
            </Typography>
            <Typography variant="h6" color="primary">
              {dashboardStats.totalUsers > 0 
                ? Math.round((dashboardStats.totalQuizResults / dashboardStats.totalUsers) * 100)
                : 0}%
            </Typography>
          </div>
          <div className="bg-purple-50 p-4 rounded-lg">
            <Typography variant="body2" color="textSecondary" className="mb-1">
              Content per Category
            </Typography>
            <Typography variant="h6" color="primary">
              {dashboardStats.totalCategories > 0 
                ? Math.round(((dashboardStats.totalQuestions || 0) + (dashboardStats.totalTips || 0)) / dashboardStats.totalCategories)
                : 0}
            </Typography>
          </div>
        </div>
      </div>
    </div>
  );
}; 