import React, { useState } from 'react';
import {
  Typography,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  Alert,
  CircularProgress,
  Chip,
  Avatar,
  LinearProgress,
} from '@mui/material';
import {
  Visibility as ViewIcon,
  Delete as DeleteIcon,
  Person,
  Assessment,
  TrendingUp,
  Schedule,
} from '@mui/icons-material';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { apiService } from '../services/api';
import { QuizResult, User, Category } from '../types';

export const QuizResults: React.FC = () => {
  const [selectedResult, setSelectedResult] = useState<QuizResult | null>(null);
  const [openDialog, setOpenDialog] = useState(false);
  const [error, setError] = useState('');

  const queryClient = useQueryClient();

  const { data: resultsResponse, isLoading } = useQuery({
    queryKey: ['quiz-results'],
    queryFn: () => apiService.getQuizResults(),
  });

  const deleteMutation = useMutation({
    mutationFn: (id: string) => apiService.deleteQuizResult(id),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['quiz-results'] });
    },
    onError: (error: any) => {
      setError(error.response?.data?.message || 'Failed to delete quiz result');
    },
  });

  const results = resultsResponse?.data?.items || [];

  const handleViewResult = (result: QuizResult) => {
    setSelectedResult(result);
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
    setSelectedResult(null);
  };

  const handleDelete = (id: string) => {
    if (window.confirm('Are you sure you want to delete this quiz result?')) {
      deleteMutation.mutate(id);
    }
  };

  const getScoreColor = (score: number, total: number) => {
    const percentage = (score / total) * 100;
    if (percentage >= 80) return 'success';
    if (percentage >= 60) return 'warning';
    return 'error';
  };

  const getScoreLabel = (score: number, total: number) => {
    const percentage = (score / total) * 100;
    if (percentage >= 80) return 'Excellent';
    if (percentage >= 60) return 'Good';
    if (percentage >= 40) return 'Fair';
    return 'Poor';
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleString();
  };

  const formatDuration = (seconds: number) => {
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    return `${minutes}m ${remainingSeconds}s`;
  };

  const getUserName = (result: QuizResult) => {
    // Handle both userId (from UserQuiz) and user (for backward compatibility)
    const user = result.user || result.userId;
    if (typeof user === 'string') {
      return user;
    }
    if (user && typeof user === 'object') {
      return user.name || 'Unknown User';
    }
    return 'Unknown User';
  };

  const getUserEmail = (result: QuizResult) => {
    // Handle both userId (from UserQuiz) and user (for backward compatibility)
    const user = result.user || result.userId;
    if (typeof user === 'string') {
      return 'N/A';
    }
    if (user && typeof user === 'object') {
      return user.email || 'N/A';
    }
    return 'N/A';
  };

  const getCategoryName = (result: QuizResult) => {
    if (result.category) {
      if (typeof result.category === 'string') {
        return result.category;
      }
      if (typeof result.category === 'object') {
        return result.category.name || 'Unknown Category';
      }
    }
    return 'General Quiz'; // Default category for quiz results
  };

  const getScorePercentage = (result: QuizResult) => {
    if (result.totalQuestions > 0) {
      return Math.round((result.score / result.totalQuestions) * 100);
    }
    return 0;
  };

  const getDuration = (result: QuizResult) => {
    // Use timeSpent from UserQuiz model, fallback to duration
    const timeInSeconds = result.timeSpent || result.duration || 0;
    return formatDuration(timeInSeconds);
  };

  if (isLoading) {
    return (
      <div className="flex justify-center items-center min-h-[400px]">
        <CircularProgress />
      </div>
    );
  }

  if (results.length === 0) {
    return (
      <div>
        <div className="flex justify-between items-center mb-6">
          <Typography variant="h4">Quiz Results</Typography>
          <Typography variant="body2" color="text.secondary">
            0 total results
          </Typography>
        </div>
        <div className="bg-white rounded-lg shadow-md p-12 text-center">
          <Typography variant="h6" color="text.secondary" className="mb-2">
            No Quiz Results Found
          </Typography>
          <Typography variant="body2" color="text.secondary">
            Quiz results will appear here once users complete assessments.
          </Typography>
        </div>
      </div>
    );
  }

  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <Typography variant="h4">Quiz Results</Typography>
        <Typography variant="body2" color="text.secondary">
          {results.length} total results
        </Typography>
      </div>

      {error && (
        <Alert severity="error" className="mb-6">
          {error}
        </Alert>
      )}

      <div className="space-y-4">
        {results.map((result: QuizResult) => (
          <div key={result._id} className="bg-white rounded-lg shadow-md p-6">
            <div className="flex items-start justify-between mb-4">
              <div className="flex items-center gap-3">
                <Avatar className="bg-blue-500">
                  {getUserName(result).charAt(0).toUpperCase()}
                </Avatar>
                <div>
                  <Typography variant="h6">{getUserName(result)}</Typography>
                  <Typography variant="body2" color="text.secondary">
                    {getUserEmail(result)}
                  </Typography>
                </div>
              </div>
              <div className="flex gap-1">
                <IconButton
                  size="small"
                  onClick={() => handleViewResult(result)}
                  color="primary"
                >
                  <ViewIcon />
                </IconButton>
                <IconButton
                  size="small"
                  onClick={() => handleDelete(result._id)}
                  color="error"
                >
                  <DeleteIcon />
                </IconButton>
              </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-4">
              <div className="text-center">
                <Typography variant="h4" color="primary">
                  {result.score}/{result.totalQuestions}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Score
                </Typography>
              </div>
              <div className="text-center">
                <Typography variant="h4" color="secondary">
                  {getScorePercentage(result)}%
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Percentage
                </Typography>
              </div>
              <div className="text-center">
                <Typography variant="h4" color="info.main">
                  {getDuration(result)}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Duration
                </Typography>
              </div>
              <div className="text-center">
                <Typography variant="h4" color="success.main">
                  {result.correctAnswers}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Correct
                </Typography>
              </div>
            </div>

            <div className="space-y-3">
              <div>
                <div className="flex justify-between items-center mb-1">
                  <Typography variant="body2" color="text.secondary">
                    Performance
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    {getScoreLabel(result.score, result.totalQuestions)}
                  </Typography>
                </div>
                <LinearProgress
                  variant="determinate"
                  value={(result.score / result.totalQuestions) * 100}
                  color={getScoreColor(result.score, result.totalQuestions) as any}
                  className="h-2 rounded"
                />
              </div>

              <div className="flex items-center gap-4 text-sm text-gray-600">
                <div className="flex items-center gap-1">
                  <Schedule fontSize="small" />
                  <span>{formatDate(result.completedAt)}</span>
                </div>
                <div className="flex items-center gap-1">
                  <Assessment fontSize="small" />
                  <span>{getCategoryName(result)}</span>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      <Dialog open={openDialog} onClose={handleCloseDialog} maxWidth="md" fullWidth>
        <DialogTitle>Quiz Result Details</DialogTitle>
        <DialogContent>
          {selectedResult && (
            <div className="space-y-4">
              <div className="bg-gray-50 p-4 rounded-lg">
                <Typography variant="h6" className="mb-2">User Information</Typography>
                <div className="flex items-center gap-3">
                  <Avatar className="bg-blue-500">
                    {getUserName(selectedResult).charAt(0).toUpperCase()}
                  </Avatar>
                  <div>
                    <Typography variant="subtitle1">{getUserName(selectedResult)}</Typography>
                    <Typography variant="body2" color="text.secondary">
                      {getUserEmail(selectedResult)}
                    </Typography>
                  </div>
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div className="bg-blue-50 p-4 rounded-lg text-center">
                  <Typography variant="h4" color="primary">
                    {selectedResult.score}/{selectedResult.totalQuestions}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    Final Score
                  </Typography>
                </div>
                <div className="bg-green-50 p-4 rounded-lg text-center">
                  <Typography variant="h4" color="success.main">
                    {getScorePercentage(selectedResult)}%
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    Success Rate
                  </Typography>
                </div>
              </div>

              <div className="space-y-2">
                <Typography variant="h6">Performance Details</Typography>
                <div className="grid grid-cols-2 gap-4 text-sm">
                  <div>
                    <strong>Correct Answers:</strong> {selectedResult.correctAnswers}
                  </div>
                  <div>
                    <strong>Incorrect Answers:</strong> {selectedResult.totalQuestions - selectedResult.correctAnswers}
                  </div>
                  <div>
                    <strong>Duration:</strong> {getDuration(selectedResult)}
                  </div>
                  <div>
                    <strong>Category:</strong> {getCategoryName(selectedResult)}
                  </div>
                </div>
              </div>

              <div className="space-y-2">
                <Typography variant="h6">Question Analysis</Typography>
                {selectedResult.questions?.map((question, index) => (
                  <div key={index} className="border-l-4 border-gray-200 pl-4 py-2">
                    <Typography variant="body2" className="mb-1">
                      <strong>Q{index + 1}:</strong> {question.text}
                    </Typography>
                    <div className="flex items-center gap-2">
                      <Chip
                        label={question.isCorrect ? 'Correct' : 'Incorrect'}
                        size="small"
                        color={question.isCorrect ? 'success' : 'error'}
                      />
                      {!question.isCorrect && (
                        <Typography variant="caption" color="text.secondary">
                          Correct: {question.correctAnswer}
                        </Typography>
                      )}
                    </div>
                  </div>
                ))}
              </div>

              <div className="text-sm text-gray-600">
                <strong>Completed:</strong> {formatDate(selectedResult.completedAt)}
              </div>
            </div>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog}>Close</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}; 